<script>
import MainSearch from '../MainHeaderBoxComp/RightNavComp/MainSearch.vue';
export default {
    name: "RightNav",
    data() {
        return {
            userS: "none"
        };
    },
    mounted() {
        document.addEventListener("mouseup", (e) => {
            const userOn = document.getElementById("navMenu");
            const selfOn = document.getElementById("self");
            if (userOn) {
                if (!userOn.contains(e.target) && !selfOn.contains(e.target)) {
                    this.userS = "none";
                }
            }
        });
    },
    methods: {
        userSelf() {
            if (this.userS === "block") {
                this.userS = "none";
            }
            else {
                this.userS = "block";
            }
            //我也不知道为什么if总是能成立，反正就是有问题，但我找不出来
            //这个东西可有可无，影响不大，不用管
        }
    },
    components: { MainSearch }
}
</script>

