<template>
  <div class="entry-list-wrap">
    <div name="entry-list" tag="div" class="entry-list list" ad-list>
      <RecycleScroller
      style="height:auto"
      class="scroller"
      :items="items"
      :item-size="140"
      v-if="items.length"
      >
      <HomeArticleList></HomeArticleList>
    </RecycleScroller>
    </div>
  </div>
</template>

<script>
import HomeArticleList from "./HomeArticleList.vue";
export default {
    components: { HomeArticleList },
    data () {
    return {
      lastData:5,
      newData:0,
      items: []
    }
  },
  created () {
    this.getData()
    window.addEventListener('scroll', this.Loading, true); 
  },
  methods: {
    getData () {
      for (let i = 0; i < this.lastData; i++) {
        this.items.push({id : i})
      }
    },
    Loading() {
        let _this=this
        window.onscroll = function() {
          let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
          let clientHeight = document.documentElement.clientHeight;
          let scrollHeight = document.documentElement.scrollHeight;
          if (scrollTop + clientHeight >= scrollHeight) {
          _this.newData=_this.lastData+5
          for (let i = _this.lastData; i < _this.newData; i++) {
            _this.items.push({id : i})
          }
          _this.lastData=_this.newData
          }
        }
      }
  },
}
</script>

<style>
.entry{
    cursor: pointer;
    position: relative;
    background: #fff;
    padding: 12px 20px 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.entry-list {
  width: 100%;
  background-color: #fff;
  position: relative;
}
</style>