<template>
  <li class="search-add">
    <ul class="search-add-ul">
      <li class="nav-item search">
        <form role="search" class="search-form" :class="searchFormActive">
          <input
            type="search"
            maxlength="32"
            :placeholder="placeholderText"
            class="search-input" @focus="handleSearchFocus"/>
          <div class="search-icon-container" :class="searchIconContainerActive">
            <img v-if="!changeImg"
              src="@/assets/searchBlack.png"
              alt="搜索"
              class="search-icon"/>
              <img v-if="changeImg"
              src="@/assets/searchBlue.png"
              alt="搜索"
              class="search-icon"/>
          </div>
          <div class="typehead" v-show="showTypehead">
            <div class="title" >
              <span >搜索历史</span>
              <span class="clear" > 清空 </span>
            </div>
          </div>
        </form>
      </li>
      <li class="nav-item add creator-item" v-if="showAddItem">
        <div class="add-group">
          <button class="add-btn">
            创作者中心
          </button>
          <div class="more" @click="showMoreList">
            <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg" class="unfold12-icon" :class="doChange" >
              <path d="M2.45025 4.82383C2.17422 4.49908 2.40501 4 2.83122 4H9.16878C9.59499 4 9.82578 4.49908 9.54975 4.82382L6.38097 8.5518C6.1813 8.7867 5.8187 8.7867 5.61903 8.5518L2.45025 4.82383Z" fill="white" >
            </path>
            </svg>
          </div>
          <ul class="more-list" v-show="showListContent">
            <li class="item" ><span class="icon write-article">写文章</span></li>
            <li class="item" ><span class="icon issue-points">发沸点</span></li>
            <li class="item" ><span class="icon create-jcode">写代码</span></li>
          </ul>
        </div>
      </li>
    </ul>
  </li>
</template>

<script>
export default {
  name: "MainSearch",
  data() {
        return {
            changeImg:false,
            showAddItem:true,
            showTypehead: false,
            showListContent:false,
            searchIconContainerActive:null,
            searchFormActive:null,
            placeholderText:'搜索稀土掘金',
            doChange:{
              active:false
            }
        };
    },  
  methods:{
    handleSearchFocus(){
      this.placeholderText='搜索文章/小册/标签/用户'
      this.showTypehead=true
      this.showAddItem = false
      this.searchFormActive = 'active'
      this.searchIconContainerActive = 'active'
      this.src='@/assets/searchBlue.png'
      this.changeImg=true
    },
    showMoreList(){
      this.doChange.active=!this.doChange.active
      this.showListContent=!this.showListContent
    }
  },
  mounted(){
    document.addEventListener('mouseup',(e)=>{
      const searchForm = document.getElementsByClassName('search-form')[0];
      const more = document.getElementsByClassName('more')[0];
      const moreList = document.getElementsByClassName('more-list')[0];
      if(searchForm){
        if(!(searchForm.contains(e.target))){
          this.showTypehead=false
          this.showAddItem = true
          this.searchFormActive=null
          this.searchIconContainerActive=null
          this.src='@/assets/searchBlack.png'
          this.changeImg=false
          this.placeholderText='搜索稀土掘金'
        }
      }
      if(moreList){
        if(!(moreList.contains(e.target))&&!(more.contains(e.target))){
          this.doChange.active=false
          this.showListContent=false
        }
      }
    })
  },
};
</script>

<style>
.search-form.active{
  width: 42rem;
  border: 1px solid #1e80ff;
}

.add-group .more-list .icon:before{
    content: "";
    display: inline-block;
    position: relative;
    background-size: 18px 18px;
    height: 18px;
    width: 18px;
    top: 4px;
    margin: 0 12px 0 2px;
}
.add-group .more-list .write-article:before{
  background-image: url(@/assets/xiewenzhang.png);
}
.add-group .more-list .issue-points:before{
  background-image: url(@/assets/fafeidian.png);
}
.add-group .more-list .create-jcode:before{
  background-image: url(@/assets/xiedaima.png);
}
.add-group .more-list .item{
    margin: 0 8px;
    padding: 6px 5px 5px 0.8rem;
    border-radius: 4px;
    line-height: 24px;
    font-weight: 400;
}
.add-group .more-list{
    position: absolute;
    top: 120%;
    left: -7px;
    right: 0;
    padding: 0.417rem 0;
    font-size: 1.167rem;
    color: #515767;
    background-color: #fff;
    display: none;
    z-index: 1;
    box-shadow: 0 0 24px rgb(81 87 103 / 16%);
    border-radius: 6px;
    width: 10rem;
    display: block;
}
.unfold12-icon.active{
  transform: rotate(180deg);
}
.add-group .add-btn:hover{
  background-color: #1171ee;
}
.add-group .more:hover{
  background-color: #1171ee;
}
.add-group .add-btn , .add-group .more{
    height: 100%;
    color: #fff;
    background-color: #1e80ff;
    border-radius: 3px;
    transition: background-color .1s linear .05s;
}
.add-group .more{
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 1.667rem;
    border-left: 1px solid hsla(0,0%,100%,.1);
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    box-sizing: border-box;
}
.add-group .add-btn{
    padding: 0 1rem;
    font-size: 1.167rem;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    white-space: nowrap;
}
.add-group{
    display: flex;
    align-items: center;
    position: relative;
    cursor: pointer;
    width: 115px;
    height: 3rem;
}
.nav-item.add{
    cursor: default;
    width: 25rem;
    transition: width .3s;
    overflow: hidden;
    padding-left: 0;
}
.nav-item:last-child{
  padding-right: 0;
}
.nav-item.add.creator-item{
    overflow: visible;
    width: 11rem;
    padding-left: 5px
}
.typehead .title .clear{
    cursor: pointer;
    color: #1e80ff;
}
.typehead .title{
      border-bottom: 1px solid #ebebeb;
    padding: 0.5rem 1rem;
    display: flex;
    justify-content: space-between;
}
.typehead {
  width: 100%;
  position: absolute;
  top: 105%;
  line-height: 2rem;
  left: 0;
  z-index: 1000;
  min-width: 6rem;
  font-size: 1rem;
  color: #869aab;
  list-style: none;
  text-align: left;
  border-radius: 0.17rem;
  box-shadow: 0 1px 2px 0 rgb(0 0 0 / 5%);
  border: 1px solid #ebebeb;
  background-color: #fff;
}

.search-add {
  flex: 1 1 auto;
  height: 5rem;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #f1f1f1;
}
.search-add-ul {
  width: 42.833rem;
  display: flex;
  justify-content: flex-start;
}
.nav-item.search {
  cursor: auto;
}
.nav-item {
  position: relative;
  color: #86909c;
  padding: 0 1rem;
  padding-left: 1rem;
  font-size: 1.167rem;
  margin: 0;
  margin-left: 0px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-form {
  width: 360px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 4px;
  position: relative;
  height: 2.834rem;
  border: 1px solid #c2c8d1;
  transition: width 0.2s;
}
.search-form .search-input {
  border: none;
  width: calc(100% - 44px);
  padding: 0.6rem 0 0.6rem 1rem;
  box-shadow: none;
  outline: none;
  font-size: 1.1rem;
  color: #8a919f;
  background-color: transparent;
  transition: width 0.3s;
}
.search-icon-container.active{
  background: #eaf2ff;
}
.search-icon-container {
  position: relative;
  left: -2px;
  width: 44px;
  height: 30px;
  background: #f2f3f5;
  border-radius: 2px;
}
.search-icon {
  display: inline-block;
  width: 1.33rem;
  padding: 0 0.8333rem 0 0;
  cursor: pointer;
  margin: 7px 15px;
}
button{
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: #007fff;
    color: #fff;
    border-radius: 2px;
    border: none;
    padding: 0.5rem 1.3rem;
    outline: none;
    transition: background-color .3s,color .3s;
    cursor: pointer;
}
li{
  list-style: none;
}
</style>