<script>

/*按时间问好*/

export default {
  name: "HomeSideBar",
  mounted() {
  var hours = new Date().getHours();
const hello = document.getElementById('hello');
if (hours<6||hours>=18){
  hello.innerText="晚上好!"
}else if (hours>=6&&hours<12){
  hello.innerText="上午好!";
}else if (hours>=12&&hours<18){
  hello.innerText="下午好!"
}


/*微信扫一扫图片展开*/
var wechat = document.getElementById('wechat');
var open = document.getElementById('sao');
wechat.onmousemove=function () {
  open.style.opacity=1;
}
wechat.onmouseout=function () {
  open.style.opacity=0;
}

/*广告投放的展开*/
function none() {
  if (this.id==="g0"){
    ion0.style.display="none";
  }else if (this.id==="g1"){
    ion1.style.display="none";
  }else if (this.id==="g2") {
    ion2.style.display="none"
  }else if (this.id==="g3"){
    ion3.style.display="none"
  }
  // console.log(this.id)
}
function disnone(){
  // console.log(this.id)
  if (this.id==="g0"){
    ion0.style.display="";
  }else if (this.id==="g1"){
    ion1.style.display="";
  }else if (this.id==="g2") {
    ion2.style.display=""
  }else if (this.id==="g3"){
    ion3.style.display=""
  }
}
var g0 =document.getElementById('g0')
var g1 = document.getElementById('g1');
var g2 = document.getElementById('g2')
var g3 = document.getElementById('g3')
var ion1 = document.getElementById('ion1')
var ion0 = document.getElementById('ion0')
var ion2 = document.getElementById('ion2')
var ion3 = document.getElementById('ion3')
g0.addEventListener("mousemove",disnone)
g0.addEventListener("mouseout",none)
g1.addEventListener("mousemove",disnone)
g1.addEventListener("mouseout",none)
g2.addEventListener("mousemove",disnone)
g2.addEventListener("mouseout",none)
g3.addEventListener("mousemove",disnone)
g3.addEventListener("mouseout",none)

/*页面下滑后，右侧栏的展开*/
var test = document.getElementById("sticky-block");
window.onscroll = function(){
  var t = document.documentElement.scrollTop || document.body.scrollTop;
  if( t >= 1500 ) {
    test.style.cssText="opacity: 1;\n" +
        "    top: 67px;\n" +
        "    z-index: 5;\n" +
        "    pointer-events: all;"
  } else {
    test.style.cssText="opacity: 0;\n" +
        "    top: 67px;\n" +
        "    z-index: -1;\n" +
        "    pointer-events: none;"
  }



}


/*广告右上角的X*/
var x0 = document.getElementById('x0');
var x1 = document.getElementById('x1');
var x2 = document.getElementById('x2');
var x3 = document.getElementById('x3')
function hx() {
  this.style.fontSize="10px";
}
function nx() {
