## 三、项目实现
### 3.1 技术选型与相关开发文档
我们小组要开发的项目是【仿掘金首页项目】，所以要完成的主要功能是：
#### 1. 无限滚动的文章列表
#### 2. 文章详情页中的TOC滚动高亮和代码高亮效果
为了方便代码开发，我们小组统一使用vue框架进行开发，并且是单页面项目。
### 3.2 架构设计
真正的掘金官网文章发布量巨大，所以若用户加载的文章列表数量庞大，可能会导致性能下降，为了解决这个问题，需要进行长列表性能优化，我们引入了第三方的库【vue-virtual-scroll】来对长列表进行优化。而由于我们小组没有大量的文章列表数据库，所以循环渲染一篇文章来作为演示。
### 3.3 项目代码介绍
#### 下拉加载更多
~~~
data () {
    return {
      //旧数据
      lastData:5,
      //新数据
      newData:0,
      items: []
    }
  },
  created () {
    this.getData()
    //添加监听事件
    window.addEventListener('scroll', this.Loading, true); 
  },
  methods: {
    getData () {
    //遍历旧列表
      for (let i = 0; i < this.lastData; i++) {
        this.items.push({id : i})
      }
    },
    Loading() {
        let _this=this
        window.onscroll = function() {
        //获取滚轮的位置
          let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
          let clientHeight = document.documentElement.clientHeight;
          let scrollHeight = document.documentElement.scrollHeight;
          //判断是否滚动到底，滚动到底列表数据增加
          if (scrollTop + clientHeight >= scrollHeight) {
          _this.newData=_this.lastData+5
          for (let i = _this.lastData; i < _this.newData; i++) {
            _this.items.push({id : i})
          }
          _this.lastData=_this.newData
          }
        }
      }
  },
~~~
#### 文章渲染
~~~
//使用计算属性与markdown-it将markdown转为HTML
computed:{
      markdown(){
        const md = new MarkDownIt()
        const result = md.render(this.content)
        return result
      }
    }
~~~
#### 文章目录导航
~~~
data() {
        return {
            catalogue:[]
        }
    },
    mounted() {
          this.$nextTick(()=>{
          //查找父组件的文章内容标签
            const articleContent = this.$parent.$parent.$parent.$refs.article
            //标记要生成目录的标签
            const titleTag=["H1","H2","H3"]
            let titles = []
            //获取文章内容中的所有子标签
            articleContent.childNodes.forEach((e,index) => {
            //判断是否为标记的标签
              if(titleTag.includes(e.nodeName)){
              
                const id = "header-"+index
                e.setAttribute("id",id)
                //插入目录数组
                titles.push({
                  id:id,
                  title:e.innerHTML,
                  level:Number(e.nodeName.substring(1,2)),
                  nodeName:e.nodeName
                })
              }
            });
            //将目录返回，使用v-for自动生成
            this.catalogue=titles
          })
    }
~~~
