import Vue from "vue";
import VueRouter from "vue-router"
import ArticleMainContainer from "@/components/ArticleComp/ArticleMainContainer.vue"
import HomeMainContainer from "@/components/HomeComp/HomeMainContainer.vue"
Vue.use(VueRouter)
export default new VueRouter({
    routes: [{path:'/article',component:ArticleMainContainer},
            {path:'/home',component:HomeMainContainer},
            {path:'/',redirect:'/home'}
    ]
})
