<template>
  <div class="all" style="padding: 0">
      <nav>
        <div class="articlecatalogue-title">目录</div>
        <div class="articlecatalogue-list">
          <ul class="TocBox">
              <li v-for="item in catalogue" class="item" :key="item.id" :style="{paddingLeft:(item.level*22)-44+'px'}">
              <a :href="'#'+item.id" class="catalog-aTag">{{item.title}}</a>
              </li>
          </ul>
        </div>
      </nav>
    </div>
</template>

<script>
export default {
    name:'ArticleToc',
    data() {
        return {
            catalogue:[]
        }
    },
    mounted() {
        	this.$nextTick(()=>{
            const articleContent = this.$parent.$parent.$parent.$refs.article
            const titleTag=["H1","H2","H3"]
            let titles = []
            articleContent.childNodes.forEach((e,index) => {
              
              if(titleTag.includes(e.nodeName)){
                const id = "header-"+index
                e.setAttribute("id",id)
                titles.push({
                  id:id,
                  title:e.innerHTML,
                  level:Number(e.nodeName.substring(1,2)),
                  nodeName:e.nodeName
                })
              }
            });
            this.catalogue=titles
          })
    }
}
</script>

<style scoped>
.item:hover{
  background-color: #e4e6eb;
}
.articlecatalogue-list {
  position: relative;
  max-height: 460px;
  margin: 8px 4px 0 0;
  overflow: auto;
}
.articlecatalogue-title {
  font-weight: 500;
  padding: 1.333rem 0;
  margin: 0 1.667rem;
  font-size: 16px;
  line-height: 2rem;
  color: #1d2129;
  border-bottom: 1px solid #e4e6eb;
}
.TocBox {
    position: relative;
    line-height: 22px;
    padding: 0 0 12px;
}
.TocBox .item {
    margin: 0;
    font-size: 1.167rem;
    font-weight: 400;
    line-height: 22px;
    color: #333;
    list-style: none;
}
.TocBox .catalog-aTag {
    color: inherit;
    display: inline-block;
    padding: 8px;
    width: 90%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    
}

</style>